import React from 'react'
// Styles
import s from './SetupReward.sass'

const SetupReward = ({ missionForm, setReward, assets, contracts }) => {
  return (
    <div className={s.container}>
      <div className={s.gift}>
        <img src="" alt=""/>
      </div>
      <div className={s.form}>
        <div className={s.tokens}>
          {missionForm.reward.contractAddress || 'Select token...'}
          <div className={s.assets}>
            {assets.map(a => (
              <div className={s.asset}>
                <img src={(contracts[a.contractAddress] || {}).imageUrl} alt={`${a.contractAddress}-${a.tokenId}`}/>
                <p>{a.name}</p>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  )
}

export default SetupReward